.navbar {
    background: #3384a4;
    min-height: 70px;
    position: fixed;
    width: 100%; 
    color: rgb(255, 255, 255);
    white-space: nowrap;
  }

  .navbar li {
    display: inline-block;
    padding: 26px;
    width: 35%;
    text-align: center;
  }
  
  .navbar a {
    text-decoration: none;
    color: white;
    font-weight: 900;
  }
  
  @media (max-width: 420px) {
    .navbar li:first-child {
      width: 25%;
    }
  
    .navbar li:nth-child(2) {
      width: 35%;
    }
  
    .navbar li:last-child {
      width: 40%;
    }
  }